table {

	td,
	th {
		border-top: 1px solid var(--neutral-2) !important;
		border-bottom: 1px solid var(--neutral-2) !important;
		border-left: none !important;
		border-right: none !important;
		padding: 8px 8px 8px 8px !important;
	}

	/* Keep left border only on the first column */
	td:first-child,
	th:first-child {
		border-left: 1px solid var(--neutral-2) !important;
	}

	/* Keep right border only on the last column */
	td:last-child,
	th:last-child {
		border-right: 1px solid var(--neutral-2) !important;
	}

	th.subscription-offers,
	td.subscription-offers {
		max-width: 638px;
		word-wrap: break-word;
	}

	td.subscription-offers>findqo-subscription-offers {
		display: block;
		margin-right: 180px;
	}
}

sub.line-through {
	text-decoration: line-through;
	font-size: 10px;
	line-height: 14px;
	color: var(--general-4);
}

sup {
	top: -0.7em;

	&.vat-description {
		top: -1.5em;
	}
}

.flex-center {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}

.no-wrap {
	white-space: nowrap !important;
}

.w-12px {
	width: 12px;
}

.fs-8px {
	font-size: 8px !important;
}